// Fonts
@import url("https://use.typekit.net/glp0pxk.css");
@import url("https://use.typekit.net/glp0pxk.css");

@import 'variables';

html {
  padding: 0;
}

body {
  // display: flex;
  // justify-content: center;
  // align-content: center;
  height: 100%;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background: #43C6AC;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to bottom, rgb(25, 22, 84), #43C6AC);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to bottom, $electricblue, $teal); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  background-size: cover;
}

#root {
  padding: 0;
  margin: 0;
}

.App {
  margin: 0;
  padding: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

nav {
  // width: 100vw;
  height: 50px;
  // margin: 1vmin 0;
  padding: 2vmin 0 2vmin 0;

  font-family: pauline, sans-serif;
  font-style: normal;
  font-size: calc(42px + (64 - 42) * ((100vw - 300px) / (1800 - 300)));
  line-height: calc(42px + (68 - 42) * ((100vw - 300px) / (1800 - 300)));
  font-weight: 700;
  text-align: center;
  
  color: $darkblue;
  background: rgba(255, 255, 255, 1);
  background-size: cover;
  -webkit-box-shadow: inset 0px -5px 15px 3px rgba(85,85,85,0.2);
  -moz-box-shadow: inset 0px -5px 15px 3px rgba(85,85,85,0.2);
  box-shadow: inset 0px -5px 15px 3px rgba(85,85,85,0.2);

  border-bottom: 1px solid #555;

  
}

#social-share {
    display: flex;
    flex-direction: row;
    justify-content: center;

    position: fixed;
    width: 200px;
    top: 30px;
    right: 10px;

    cursor: pointer;

    z-index: 3;

    #share-icon {
      width: 40px;
      height: 40px;
      padding: 0 1vmin;

      opacity: 0.5;

      transition: 400ms;

      &:hover {
        opacity: 0.8;
      }
    }
}

// FORM

form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  padding: 2vmin 0 3vmin 0;

  background-color: rgbA(119, 119, 119, 0.7);
  -webkit-box-shadow: inset 0px -10px 36px -32px rgba(0,0,0,1);
  -moz-box-shadow: inset 0px -10px 36px -32px rgba(0,0,0,1);
  box-shadow: inset 0px -10px 36px -32px rgba(0,0,0,1);

  h1 {
    margin: 0 1vmin 0 0;
    font-size: calc(24px + (32 - 24) * ((100vw - 300px) / (1800 - 300)));
    line-height: calc(28px + (36 - 28) * ((100vw - 300px) / (1800 - 300)));
    font-weight: 700;
    vertical-align: middle;
    color: #ffffff;
  }

  #input-span {
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
  }
  
  input {
    margin: 2vmin 1vmin;
    font-size: calc(18px + (24 - 18) * ((100vw - 300px) / (1800 - 300)));
    line-height: calc(24px + (28 - 24) * ((100vw - 300px) / (1800 - 300)));
    
    text-align: center;

    &[type='text']{
      // margin: 6px 0;
      padding: 1vmin 0vmin;
      box-sizing: border-box;

      font-weight: 700;
      color: #fff;
      background: $inputtext;
      // border: 1px solid white;
      // border-radius: 5px;
      overflow: hidden;

      &::placeholder {
        color: white;
      }
    }

  }

  button {
      display: block;
      margin: 1vmin 0 0 0;
      padding: 1.5vmin 6vmin;

      font-size: calc(24px + (28 - 24) * ((100vw - 300px) / (1800 - 300)));
      line-height: calc(24px + (28 - 24) * ((100vw - 300px) / (1800 - 300)));
      font-weight: 700;
      color: #fff;
      background: $button;

      border: 2px solid #fff;
      border-radius: 10px;

      overflow: visible; /* Edge hides overflow */
      text-transform: none; /* Firefox inherits text-transform */
      -webkit-appearance: button; /* Safari otherwise prevents some styles */

      transition: 0.4s;

      &:hover {
        color: #fff;
        background: $buttonhover;
        border: 2px solid #fff;
        transform: scale(1.1);
        transition: 0.4s;
      }
  }

      .select-css {
        display: flex;
        justify-content: center;
        align-items: center;
        max-width: 90%;
        margin: 2vmin 1vmin;
        padding: 1vmin 5vmin 1vmin 2vmin;

        color: #084769;
        background-color: #084769;

        font-size: calc(18px + (24 - 18) * ((100vw - 300px) / (1800 - 300)));
        font-weight: 700;
        // text-align: center;

        box-sizing: border-box;
        box-shadow: 0 1px 0 1px rgba(0,0,0,.04);
        border-radius: .5em;
        -moz-appearance: none;
        -webkit-appearance: none;
        appearance: none;
        background-color: #fff;
        background-image: url('data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%22292.4%22%20height%3D%22292.4%22%3E%3Cpath%20fill%3D%22%23007CB2%22%20d%3D%22M287%2069.4a17.6%2017.6%200%200%200-13-5.4H18.4c-5%200-9.3%201.8-12.9%205.4A17.6%2017.6%200%200%200%200%2082.2c0%205%201.8%209.3%205.4%2012.9l128%20127.9c3.6%203.6%207.8%205.4%2012.8%205.4s9.2-1.8%2012.8-5.4L287%2095c3.5-3.5%205.4-7.8%205.4-12.8%200-5-1.9-9.2-5.5-12.8z%22%2F%3E%3C%2Fsvg%3E'),
          linear-gradient(to bottom, #ffffff 0%,#e5e5e5 100%);
        background-repeat: no-repeat, repeat;
        background-position: right .7em top 50%, 0 0;
        background-size: .65em auto, 100%;

        border-color: #838f96;
        border-radius: 10px;
        
        opacity: 0.8;
        z-index: 2;

        &option {
          font-weight:normal;
          text-align: center;
        }

        &:hover {
          border-color: #888;
        }

        &:focus {
          outline: none;
        }
    }

    .select-css::-ms-expand {
        display: none;
    }
}


// CONTENT

.content {
  display: flex;
  justify-content: center;
  align-content: flex-start;
  flex-wrap: wrap;

  min-height: 80vh; 
  margin: 2vmin 0 0 0;
  padding: 0 4vmin;
}

.search-info {
  display: flex;
  justify-content: center;
  align-items: center;

  padding: 2vmin 0 1vmin 0;

  // width: 100vw;
    background: rgba(50,50,50,0.8);
    border-top: 1px solid #fff;
    border-bottom: 1px solid #fff;
}

#search-term-break, #search-word {

  font-size: calc(20px + (28 - 20) * ((100vw - 300px) / (1800 - 300)));
  line-height: calc(24px + (28 - 24) * ((100vw - 300px) / (1800 - 300)));
  font-weight: 600;
  text-align: center;

  color: #fff;
}

#search-term-break {
  margin: 0 2vmin 1vmin 1vmin;
}

#search-word {
  margin: 0 0 1vmin 0;
}

.card {
  min-width: 150px;
  margin: 1vmin;
  padding: 1vmin 2vmin;

  // color: #fff;
  // background: rgba(50, 50, 50, 0.8);
  border-radius: 10px;
  border: 1px solid #fff;

  h3 {
    margin: 0 0 1vmin 0;
    // color: white;
    font-size: calc(18px + (24 - 18) * ((100vw - 300px) / (1800 - 300)));
    line-height: calc(24px + (28 - 24) * ((100vw - 300px) / (1800 - 300)));

    font-weight: 600;
  }

  p {
      font-size: calc(16px + (18 - 16) * ((100vw - 300px) / (1800 - 300)));
      line-height: calc(20px + (24 - 20) * ((100vw - 300px) / (1800 - 300)));

      filter: brightness(0.7);
        // background: red;
  }
}

p, h3 {
  text-align: center;
}

#yellow-text {
  margin: 0 2vmin;
  color: rgb(255,255,175);
  font-weight: 700;
}

#searchtype-text {
  margin: 0 2vmin;
  color: rgb(255,255,175);
  font-weight: 700;
}

#status-message {
  visibility: hidden;
  height: 100vh;
  color: $button;
}

.empty-div {
  height: 100vh;
}

footer {
  margin: 4vmin 0;
  color: rgb(26, 22, 85);

  font-size: calc(18px + (24 - 18) * ((100vw - 300px) / (1800 - 300)));
  line-height: calc(22px + (28 - 22) * ((100vw - 300px) / (1800 - 300)));
  text-align: center;

  p {
    margin: 1vmin 0;
  }
}
