@media only screen and (max-width: 820px) {

  nav {
    height: 40px;
    padding: 6vmin 0 4vmin 0;

  }
  
  #social-share {
    top: 90%;
    left: 50%;

    #share-icon {

      padding: 0 2vmin;
      opacity: 0.85;
    }
  }

  form {
    padding: 4vmin 0;

    #input-span {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }

    h1 {
      margin: 2vmin 0 0 0;
    }

    input  {
      margin: 2vmin 0vmin;

      &[type='text']{
        padding: 2vmin 2vmin;
      }
    }

    button {
      margin: 3vmin 0 2vmin 0;
      padding: 2vmin 12vmin;
    }

    .select-css {
      padding: 2vmin 8vmin;
    }

  }


  .content {
      margin: 5vmin 0 0 0;
  }

  .search-info {
    display: flex;
    flex-direction: column;
  }

  #search-term-break {
    display: none;
  }

  #search-word {
    padding: 1vmin 0;
  }


}